<template>
  <div class="noticeDetails">
    <el-row type="flex" class="row-bg headerrow" justify="center">
      <el-col class="ibox noticeDetailsbox" style="width:1000px;">
        <div class="noticetitle">
          <h3 class="title">{{title}}</h3>
          <el-button class="backbtn" plain @click="$router.go(-1)" size="small"> 返回 </el-button>
        </div>
        <div class="detailslabel">
          <span>{{tag}}</span>
        </div>
        <div class="noticedesc">
          <p>{{desc}}</p>
        </div>
        <div class="noticecontent">
          <div v-html="content"  id="col-md-7">
          </div>
        </div>

      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data () {
    return {
      title: '',
      desc: '',
      content: '',
      islink: '',
      tag: '',
      link_url: '',
    };
  },
  methods: {
    // 获取资讯详情
    getnoticeinfo (id) {
      this.$axios.get('/Portal/News/getOne', {
        params: {
          id: id
        }
      }).then(res => {
        if (res.data.code == 200) {
          this.title = res.data.data.title;
          this.desc = res.data.data.describe;
          this.tag = res.data.data.tag;
          this.content = res.data.data.content;
          this.content = res.data.data.content.replace(/<p/g,'<p class="lineh"');
        }
      });
    },

  },
  created () {
    if (this.$route.query.id) {
      // 设置滚动条初始回到顶部
      document.body.scrollTop = document.documentElement.scrollTop = 0;
      this.islink = false;
      this.getnoticeinfo(this.$route.query.id);
    } else {
      this.$message({
        type: 'error',
        message: '请求参数错误'
      })
    }

  },
}
</script>

<style lang='scss' scoped>
.noticeDetailsbox {
  min-height: 70vh;
  padding: 20px 40px;
}
.detailslabel {
  clear: both;
  width: 60px;
  height: 20px;
  text-align: center;
  border: 1px solid #EA5413;
  color: #EA5413;
  border-radius: 4px;
  margin: 10px 0px 15px 0px;
  font-size: 11px;
}
.noticedesc {
  text-indent: 2em;
  background: #eeeeee;
  font-size: 14px;
  line-height: 25px;
  padding: 20px;
}
.noticecontent {
  padding: 20px 0px;
}
.noticetitle {
  width: 100%;
  position: relative;
}
.title {
  float: left;
  line-height: 40px;margin-bottom: 5px;
  font-size: 18px;
}
.backbtn {
  float: right;
  margin-left: 15px;
  border: 1px solid #EA5413;
  color: #EA5413;
}
.backtop {
  height: 100%;
  width: 100%;
  background-color: #f2f5f6;
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.12);
  text-align: center;
  line-height: 40px;
  color: #1989fa;
}
>>>.lineh{
      font-family: Verdana,Arial,Helvetica,sans-serif;
    line-height:30px;
}
>>>.lineh img{
    max-width: 100%;
    margin-bottom: 16px;
    height: auto;
}
>>>.wscnph{
    max-width: 100%;
}
</style>


