<template>
  <div class="perform ibox">
     <div>园区走廊 空中花园</div>
     <div>
         <p>露台花园到处都有，但是在一万平米的空中花园办公是怎样的体验，园区内外被绿植包裹，在办公室与花园之间来回穿梭，随处都是你的冥想空间，解压场所激发出活力和灵感。</p>
         <div class="pic">
             <div class="fr">
                 <div><img :src="pic1"></div>
                 <div><img :src="pic2"></div>
             </div>
             <div class="fr">
                 <div><img :src="pic3"></div>
                 <div><img :src="pic4"></div>
             </div>
         </div>
     </div>
  </div>
</template>

<script>
import pic1 from '../../assets/de/kzhy_fst.png'
import pic2 from '../../assets/de/kzhy_hys.png'
import pic3 from '../../assets/de/kzhy_dyzl.png'
import pic4 from '../../assets/de/home_img_yqzl.png'
export default {
    data(){
        return{
            pic1,pic2,pic3,pic4
        }
    },
}
</script>

<style scoped lang="scss">
    .perform>div:first-of-type{
        font-size: 20px;
        font-weight: 600;
        margin: 10px 0;
    }
    .perform>div>p{
        text-indent: 2em;
        color: #414141;
        margin: 20px 0;
    }
    .perform{
        width: 1000px;
        margin: 10px auto;padding: 30px;
        box-sizing: border-box;
    }
    .fr{
        display:flex ;
        justify-content: space-between;
        margin-bottom: 20px;
        height: 250px;
    }
    .fr>div{
        width: 49%;
        height: 100%;
    }
    .pic img{
        border-radius: 10px;
        height: 100%;
    }
    .pic img:nth-of-type(1),.pic img:nth-of-type(2){
        width: 100%;
    }
</style>


