<template>
  <div class="notice">
    <el-row type="flex" class="row-bg headerrow" justify="center">
      <el-col style="width:1000px;">
        <div class="ibox">
          <div>
            <el-button :key="index + 'noticetype'" v-for="(item,index) in noticetypes" :class="activeClass == index?'activebtn':'' + 'noticetype'" size="small" @click="changetype(index,item.id)">{{item.type_name}}</el-button>
          </div>
          <div class="noticecard">
            
            <el-card class="cards" :key="index + 'newslist'" v-for="(i,index) in newslist" >
              <el-image :src="i.image" class="cardimage" @click="godetails(i)">
                <div slot="error" class="image-slot">
                  <div style="width: 100%;height: 140px;text-align: center;position:relative;border: 1px dashed #ccc;background:#d2d2d2d2;">
                    <i class="el-icon-picture-outline" style="margin-top: 70px;"></i>
                  </div>

                </div>
              </el-image>
              <div style="padding: 10px 20px;" @click="godetails(i)">
                <h3 class="cardtitle">{{i.title}}</h3>
                <span class="cardcontent">{{i.describe}}</span>
                <div class="cardfooter">
                  <span class="readtext readbtn" @click="godetails(i)">阅读全文</span>
                </div>
              </div>
            </el-card>
          </div>
          <div class="noticefooter">
            <div class="pagination">
              <el-pagination @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[8]" :page-size="8" layout=" prev, pager, next, jumper" :total="total">
              </el-pagination>
            </div>

          </div>

        </div>
      </el-col>
    </el-row>
  </div>

</template>

<script>
export default {
  data () {
    return {
      noticetypes: [],
      activeClass: '0',
      page: 1,
      pagesize: 8,
      total: 0,
      currentPage: 1,
      type: '',
      newslist: [],
    };
  },
  methods: {
    // 跳转详情页
    godetails (item) {
      console.log(item);
      if (item.content_type == 1) {
        this.$router.push({ name: 'noticeDetails', query: { id: item.id } });
      } else {
        window.open(item.link_url);
      }

    },
    //   获取资讯类型
    getnewstype () {
      this.$axios.get('/Portal/News/Type', {
      }).then(res => {
        this.noticetypes = res.data.data;
        this.changetype('0', this.noticetypes[0].id)
      });
    },
    // 
    changetype (index, type) {
      this.type = type
      this.activeClass = index;
      let params = {
        type: Number(type),
        page: this.page,
        page_size: this.pagesize
      }
      this.$axios.get('/Portal/News/getList', {
        params: params
      }).then(res => {
        this.total = res.data.data.total;
        this.newslist = res.data.data.list;
      });
    },
    handleCurrentChange (val) {
      // console.log(`当前页: ${val}`);
      let params = {
        type: this.type,
        page: val,
        page_size: this.pagesize
      }
      this.$axios.get('/Portal/News/getList', {
        params: params
      }).then(res => {
        this.total = res.data.data.total;
        this.newslist = res.data.data.list;
      });
    }
  },
  created () {
    this.getnewstype()
  },
}
</script>

<style >
.notice .noticetype {
  border: 1px solid #EA5413;
  color: #EA5413;
  border-radius: 6px;
}
.notice .noticetype:hover {
  background: #EA5413;
  color: #fff;
  border-radius: 6px;
}
.notice .activebtn {
  background: #EA5413 !important;
  color: #fff !important;
  border-radius: 6px;
}
.notice .cards {
  width: calc(25% - 13px);
  margin: 10px 5px;
  border-radius: 10px;
  display: inline-block;
  cursor: pointer;
}
.notice .cardimage {
  width: 100%;
  height: 140px;
}
.notice .el-card__body {
  padding: 0px !important;
}
.notice .cardcontent {
  font-size: 12px;
  line-height: 17px;
  width: 100%;
  display: inline-block;
  height: 50px;
  color: rgba(0, 0, 0, 0.6);
  word-break: break-word;
  display: -webkit-box;
  white-space: pre-wrap;
  word-wrap: break-word;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  margin-bottom: 10px;
}
.notice .cardfooter {
  position: relative;
}
.notice .readtext {
  font-size: 13px;
}
.notice .readbtn {
  float: right;
  margin-bottom: 10px;
  cursor: pointer;
}
.notice .btnimg {
  width: 10px;
}
.notice .cardtitle {
  line-height: 20px;
  height: 40px;
  display: -webkit-box;
  word-break: normal;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  margin-bottom: 5px;
}
.notice .noticefooter {
  position: relative;
  height: 50px;
}
.notice .pagination {
  float: right;
  padding: 20px;
}
</style>


