<template>
  <div class="niceworkapp">
    <img style="position:absolute;width:100%;height:auto;" src="../assets/web/ydb_bg.png" alt="">
    <el-row type="flex" class="row-bg headerrow" justify="center">
      <el-col class="niceworkappinside">
        <div class="info">
          <span style="color:#717071;">扫码下载</span>
          <span style="color:#EA5413;">NICEWORK APP</span>
        </div>
        <div class="messageinfo">
          <span>借用我们简单，灵活的全纳式整体解决方案节省时间。</span>
        </div>
        <div>
          <!-- 生成二维码 -->
          <div id="qrcode"></div>
          <img class="imgage" src="../assets/web/ydb_sb.png" alt="">
          <div class="icon">
            <img class="imgspancenter" src="../assets/web/icon_ios.png">
            <span  class="imgspancenter">ios</span>
            <img class="imgspancenter" style="margin-left:30px;" src="../assets/web/icon_android.png">
            <span  class="imgspancenter" >Android</span>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import QRCode from "qrcodejs2"; // 引入qrcode
export default {
  data () {
    return {

    };
  },
  methods: {


  },
  mounted () {
    new QRCode("qrcode", {
      width: 150,
      height: 150,
      text: 'http://h5.nicework.net.cn/admin/jumptodownload.html', // 二维码地址
      colorDark: "#EA5413",
      colorLight: "#fff",
    });
  },
}
</script>

<style >
.niceworkapp {
  width: 100%;
  height: 100vh;
}
.niceworkapp .niceworkappinside {
  width: 1000px;
}
.niceworkapp .info{
  margin-top: 10vh;
  font-size:25px;
  font-weight: 700;
}
.niceworkapp .messageinfo{
  margin-top: 10px;
  font-size:16px;
  letter-spacing:3px;
  font-weight: 800;
  color:rgb(153, 153, 153);;
}
/* 二维码样式 */
.niceworkapp #qrcode {
  display: inline-block;
  margin-top: 10vh;
}
.niceworkapp #qrcode img {
  background-color: #fff;
  padding: 6px;
}
.niceworkapp .imgage{
  position: absolute;
  display: inline-block;
  margin-top: 10vh;
  width: 600px;
  left: 40%;
}
.niceworkapp .imgspancenter{
  display: inline-block;
  font-size: 13px;
  padding: 0px 0; 
}
</style>


