<template>
  <div>
    <el-row type="flex" class="row-bg" justify="center">
     <el-col style="width:1100px !important;" class="hander">
        <div class="infomation ">
          <div class="left">
            <p>联系我们</p>
            <p>首开文投（北京）文化科技有限公司</p>
            <p>地址：北京市朝阳区壹线国际 蜂巢传媒艺术工作区 7-10</p>
            <p>电话：85755120</p>
            <p>传真：85755210</p>
            <p>Email：shoukaiwentou@163.com</p>
            <p>邮编：100025</p>
          </div>
          <div class="rightcord">
            <div>
              <div id="contactqrcode11"></div>
              <span>NICEWORK APP</span>
            </div>
            <div>
              <div id="contactqrcode22"></div>
              <span>关注NICEWORK公众号</span>
            </div>
          </div>
        </div>
        <baidu-map id="allmap" :center="{lng:116.511693,lat:39.914999} " :zoom="15" style="height:360px">
          <bm-marker :position="{lng: 116.511693, lat:39.914999}" title="NICE WORK"  @click="infoWindowOpen()">
            <bm-info-window title="NICE WORK" :position="{lng: 116.511693, lat: 116.511693}" style="width:380px;height:50px;" :show="showFlag" @close="infoWindowOpen()">
              <div class='contMap'>北京市朝阳区壹线国际 蜂巢传媒艺术工作区 7-10</div> 
            </bm-info-window>
          </bm-marker>
        </baidu-map>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import QRCode from "qrcodejs2"; // 引入qrcode
import { BmlMarkerClusterer } from 'vue-baidu-map'
export default {
  components: {
    BmlMarkerClusterer

  },
  data () {
    return {
      showFlag: true,
    }
  },
  methods: {
    infoWindowOpen () {
      this.showFlag = !this.showFlag;
    },
  },
  mounted () {
    new QRCode("contactqrcode11", {
      width: 70,
      height: 70,
      correctLevel: 0,
      text: 'http://h5.nicework.net.cn/admin/jumptodownload.html', // 二维码地址
      colorDark: "#000",
      colorLight: "#fff",
    });
    new QRCode("contactqrcode22", {
      width: 70,
      height: 70,
      correctLevel: 0,
      text: 'http://weixin.qq.com/r/hDlqcsHEBJx7rYUR92yW', // 二维码地址
      colorDark: "#000",
      colorLight: "#fff",
    });
  },
}
</script>

<style lang="scss" scoped>
.rightcord >>> img {
  margin: 20px auto;
}
.left > p:first-of-type {
  font-size: 25px;
  font-weight: 600;
  margin-bottom: 20px;
  color: black;
}
.left > p:nth-of-type(2) {
  color: rgba(63, 63, 63, 0.733);
  font-weight: 600;
  margin-bottom: 10px;
}
.left > p {
  line-height: 25px;
  color: rgba(63, 63, 63, 0.61);
}
>>> .BMap_bubble_title {
  color: #ea5413;
  padding: 10px;
}
>>> .contMap {
  padding: 10px;
}
.infomation {
  display: flex;
  justify-content: space-between;
}
.hander {
  border-radius: 10px;
  margin: 10px 0;
  padding: 30px;
  background-color: #fff;
}
.infomation {
  width: 100%;
  height: 230px;
}
#allmap {
  width: 100%;
  height: 300px;
  border: 1px solid #ddd;
}
.rightcord {
  display: flex;
  justify-content: space-between;
}
.rightcord img {
  display: inline-flex;
  margin-top: 25px;
  width: 80px;
}
.rightcord > div {
  text-align: center;
  margin: 0 20px;
}
.rightcord div > span {
  display: inline-block;
  color: rgba(46, 46, 46, 0.767);
  font-size: 11px;
  transform: translateY(-10px);
}
</style>


