<template>
  <div id="app">
    <header>
      <pageheader></pageheader>
    </header>
    <article :class="$route.name != 'login'?'acticlecss':''">
     <keep-alive>
      <router-view v-if="$route.meta.keepAlive"></router-view>
    </keep-alive>
    <router-view v-if="!$route.meta.keepAlive"></router-view>
    </article>
    <footer v-if="$route.name != 'login'">
      <pagefooter></pagefooter>
    </footer>
     <!-- 返回顶部 -->
          <el-backtop></el-backtop>
  </div>
</template>

<script>
import pagefooter from '@/components/footer'
import pageheader from '@/components/header'
export default {
  name: 'App',
  components: {
    pagefooter,
    pageheader
  },

}
</script>

<style lang="scss" scoped>
#app {
  position: relative;
  min-height: 100vh;
  min-width: 1300px;
  background: #f2f2f2;
  overflow-y: hidden;
}

.acticlecss {
  /* 空出底部fotter距离 */
  padding-bottom: 150px;
}
footer {
  position: absolute;
  bottom: 0;
  width: 100%;
}
>>> .el-backtop{
  color: #EA5413;
}
</style>



