import Vue from 'vue'
import Router from 'vue-router'
import login from '@/view/login'
import contact from '@/view/contact'
import contactDetails from '@/view/contactDetails'
import index from '@/view/index'
import niceWorkApp from '@/view/niceWorkApp'
import notice from '@/view/notice'
import noticeDetails from '@/view/noticeDetails'
import officeEnvironment from '@/view/officeEnvironment'
import about from '@/view/about'
import reserve from '@/view/reserve'
import uniDetails from '@/view/uniDetails'
import privacyPolicy from '@/view/privacyPolicy'
import LegalNotices from '@/view/LegalNotices'
import deGarden from '@/view/de/de_garden'
import deSell from '@/view/de/de_sell'
import dePerform from '@/view/de/de_perform'
import deMeet from '@/view/de/de_meet'
const originalPush = Router.prototype.push

Router.prototype.push = function push (location) {
  return originalPush.call(this, location).catch(err => err)
}

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/LegalNotices',
      name: 'LegalNotices',
      component: LegalNotices
    },
    {
      path: '/privacyPolicy',
      name: 'privacyPolicy',
      component: privacyPolicy
    },
    {
      path: '/about',
      name: 'about',
      component: about
    },
    {
      path: '/deGarden',
      name: 'deGarden',
      component: deGarden
    },
    {
      path: '/deSell',
      name: 'deSell',
      component: deSell
    },
    {
      path: '/dePerform',
      name: 'dePerform',
      component: dePerform
    },
    {
      path: '/deMeet',
      name: 'deMeet',
      component: deMeet
    },
    {
      path: '/uniDetails',
      name: 'uniDetails',
      component: uniDetails
    },
    {
      path: '/contact',
      name: 'contact',
      component: contact
    }, 
    {
      path: '/contactDetails',
      name: 'contactDetails',
      component: contactDetails
    },
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/niceWorkApp',
      name: 'niceWorkApp',
      component: niceWorkApp
    },
    {
      path: '/notice',
      name: 'notice',
      component: notice,
      meta: {
        keepAlive: true
      }
    },
    {
      path: '/reserve',
      name: 'reserve',
      component: reserve,
      meta: {
        keepAlive: true
      }
    },
    {
      path: '/noticeDetails',
      name: 'noticeDetails',
      component: noticeDetails
    },
    {
      path: '/officeEnvironment',
      name: 'officeEnvironment',
      component: officeEnvironment
    },
  ]
})



