<template>
  <div class="login">
    <img style="position:absolute;width:100%;height:100vh;top:0" src="../assets/web/loding_bg.png" alt="">
    <el-row type="flex" class="row-bg headerrow" justify="center">
      <el-col class="logininside">
        <div style="height:calc(100vh - 100px);">
          <el-card class="logincard" :body-style="{ padding: '15px' }">
            <img src="../assets/web/default_logo.png" class="image">
            <el-form :model="loginForm" :rules="rules" class="loginfrom" ref="loginForm" @keyup.enter.native="submitForm">
              <el-form-item prop="username">
                <el-input class="username" placeholder=" 请输入用户名" maxlength="11" oninput="value=value.replace(/[^\d]/g,'')"  clearable prefix-icon='el-icon-mobile-phone' v-model="loginForm.username">
                  </el-input>
              </el-form-item>
              <el-form-item prop="password">
                <el-input class="password"   @input="changeLock" :type="lock === 'lock' ? 'password' : 'text'" clearable placeholder=" 请输入密码"  :prefix-icon="'el-input__icon el-icon-' + lock" show-password v-model="loginForm.password">
                </el-input>
              </el-form-item>
              <el-form-item>
                <div style="position: relative; text-align: center;margin-top:20px;">
                  <el-button class="button" type="primary" @click="submitForm" style="width: 100%;">立 即 登 录</el-button>
                </div>
              </el-form-item>
            </el-form>

          </el-card>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data () {
    const checkPassword = (rule, value, callback) => {
      if (value.length < 8 || value.length > 20) {
        return callback(new Error("请输入正确的密码！"));
      } else {
        callback();
      }
    };
    return {
      lock: "lock",
      loginForm: {
        username: "",
        password: "",
      },
      rules: {
        username: [{ required: true, message:'请输入用户名',trigger: "blur" }],
        password: [{ validator: checkPassword, trigger: "blur" }],
      },
    };
  },
  methods: {
    login () {
      this.$axios.get('/Api/User/login', {
        params: this.loginForm
      }).then(res => {
        if(res.data.code == 200){
          let userdetails = {
                    id: res.data.data.userInfo.id,
                    username: res.data.data.userInfo.username,
                    nick_name: res.data.data.userInfo.nickname,
                    header_img: res.data.data.userInfo.avatar,
                    email: res.data.data.userInfo.email,
                    company_id: res.data.data.userInfo.company_id,
                    company_name: res.data.data.userInfo.company_name,
                    role: res.data.data.userInfo.role,
                    menus: res.data.data.menus,
                    expire: res.data.data.expire

                }
           localStorage.setItem('menus', JSON.stringify(res.data.data.menus))
           localStorage.setItem('userInfo', JSON.stringify(userdetails))
           localStorage.setItem('token', res.data.data.token)
           window.location =  process.env.BASE_homepage +'web/#/layout/controller'
          
        }else{
          this.$message({
            type:'error',
            message:res.data.msg
          })
        }
      });
    },
    submitForm () {
      this.$refs["loginForm"].validate((v) => {
        if (v) {
          this.login();
        } else {
          this.$message({
            type: "error",
            message: "请正确填写登录信息",
            showClose: true,
          });
          return false;
        }
      });
    },
    changeLock () {
      this.lock === "lock" ? (this.lock = "unlock") : (this.lock = "lock");
    },

  },
  mounted () {
  },
  created(){
    if (window.location.host.indexOf("wwww") == -1) {
      window.location.host = 'www.nicework.net.cn'
      window.reload()
    }
    if(localStorage.getItem('token')){
       window.location =  process.env.BASE_homepage +'web/#/layout/controller'
    }
    
  }
}
</script>

<style lang="scss" scoped>
.login {
  width: 100%;
  height:calc(100vh - 100px); ;
}
.logininside {
  width: 1000px;
}
.logincard {
  width: 400px;
  height: 400px;
  position: absolute;
  left: calc(50% - 200px);
  top: calc(50% - 200px);
  border: 2px solid #61616175;
  box-shadow: 0px 2px 15px #61616175;
  border-radius: 10px;
  background: #fffefe;
}
.image {
    position: relative;
  margin: 30px 0;
  left: 50%;
  transform: translateX(-50%);
}
.button {
  background: #EA5413;
  border: 1px solid #EA5413;
  color: #ffff;
  box-sizing: border-box;
  padding: 20px 20px;
  transform: translateY(10px);
}
.button:hover {
  background: #EA5413;
  border: 1px solid #EA5413;
  color: #ffff;
}
.button:focus {
  background: #EA5413;
  border: 1px solid #EA5413;
  color: #ffff;
}
.loginfrom {
  // width: 360px;
  padding: 10px 20px;
}
>>> .el-input__inner{
  height:50px;
  background-color: #fff !important;
}
>>> .el-input__inner:focus {
 border: 1px solid #EA5413;
}
>>> .el-input__icon{
   margin-top: 5px;   
   font-size: 18px;
   width: 40px;
}
>>>.el-input--prefix .el-input__inner{
  padding-left: 45px;
}
>>> .el-icon-mobile-phone:after,
>>> .el-icon-unlock:after,
>>> .el-icon-lock:after {
  content: "";
  margin-left: 8px;
  margin-top: -5px;
  height: 22px;
  width: 1px;
  background: rgba(0,0,0,0.3);
}
</style>


