<template>
  <div class="perform ibox">
     <div>会客区 洽谈区</div>
     <div>
         <p>客户来访、人员面试、要事会谈都需要一个脱离办公室的会客场所，园区内多方位设立了开放式会客区、专属洽谈区提供给企业使用。</p>
         <div class="pic">
             <div class="fr">
                 <div><img :src="pic1"></div>
                 <div><img :src="pic3"></div>
             </div>
             <img :src="pic2">
         </div>
     </div>
  </div>
</template>

<script>
import pic1 from '../../assets/de/hkt_zl.png'
import pic3 from '../../assets/de/hkt_xzl.png'
import pic2 from '../../assets/de/hkt_dt.png'
export default {
    data(){
        return{
            pic1,pic2,pic3
        }
    },
}
</script>

<style scoped lang="scss">
    .perform>div:first-of-type{
        font-size: 20px;
        font-weight: 600;
        margin: 10px 0;
    }
    .perform>div>p{
        text-indent: 2em;
        color: #414141;
        margin: 20px 0;
    }
    .perform{
        width: 1000px;
        margin: 10px auto;padding: 30px;
        box-sizing: border-box;
    }
    .fr{
        display:flex ;
        justify-content: space-between;
        margin-bottom: 20px;
        height: 250px;
    }
    .fr>div{
        width: 49%;
        height: 100%;
    }
    .pic img{
        border-radius: 10px;
        height: 100%;
    }
    .pic img:nth-of-type(1),.pic img:nth-of-type(2){
        width: 100%;
    }
</style>


