<template>
  <div class="header">
    <div class=" headerrow">
      <div style="width:1100px !important;margin:0 auto;">
        <div style="display:inline-block;">
          <div class="headerleft">
            <img src="../assets/web/default_logo.png" style="width:100%;vertical-align: super;" alt="">
          </div>
        </div>
        <div style="display:inline-block;width:500px;">
          <el-menu :default-active="activeIndex" class="el-menu-demo menus" mode="horizontal" @select="handleSelect" background-color="#fff" text-color="rgba(0,0,0,0.6)" active-text-color="#EA5413">
            <el-menu-item index="1" @click="$router.push('/')">首页</el-menu-item>
            <el-menu-item index="2" @click="$router.push('/niceWorkApp')">NICEWORK移动版</el-menu-item>
            <el-menu-item index="3" @click="$router.push('/notice')">资讯·活动</el-menu-item>
            <el-menu-item index="4" @click="$router.push('/about')">关于我们</el-menu-item>
          </el-menu>
        </div>
        <div style="display:inline-block;float:right;">
          <div class="headerright">
            <el-button class="loginbtn" v-if="docontrol" plain @click="logout()" size="mini"> 退出登录 </el-button>
            <el-button class="loginbtn" v-if="docontrol" plain @click="pagetocontrol()" size="mini"> 管理中心 </el-button>
            <el-button class="loginbtn" v-if="!docontrol" plain @click="$router.push('/login')" size="mini"> 登录 </el-button>
            <span class="phonenum"> <i class="el-icon-phone-outline"> </i> {{phonenum}}</span>
          </div>
        </div>
      </div>

    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      activeIndex: '1',
      docontrol: false,
      phonenum:'010-85755120'
    };
  },
  methods: {
    logout () {
      localStorage.clear()
      window.location.href = process.env.BASE_homepage
      window.location.reload()
    },
    // 获取客服电话
    getphone () {
      this.$axios.get('/Portal/UnionOffice/getPhone', {
      }).then(res => {
        if (res.data.code == 200) {
        this.phonenum = res.data.data.phone;
        } else {
          this.$message({
            type: 'error',
            message: res.data.msg
          })
        }
      });
    },
    handleSelect (key, keyPath) {
      //   console.log(key, keyPath);
    },
    pagetocontrol () {
      window.location =  process.env.BASE_homepage +'web/#/layout/controller'
    }
  },
  mounted () {
    if (this.$route.name == 'index') {
      this.activeIndex = '1'
    } else if (this.$route.name == 'niceWorkApp') {
      this.activeIndex = '2'
    } else if (this.$route.name == 'notice' || this.$route.name.indexOf('notice') != -1) {
      this.activeIndex = '3'
    } else if (this.$route.name == 'about') {
      this.activeIndex = '4'
    } else {
      this.activeIndex = '5'
    }
  },
  created () {
    this.getphone()
    if (localStorage.getItem('token')) {
      if (localStorage.getItem('token') != null) {
        this.docontrol = true
      } else {
        this.docontrol = false
      }
    } else {
      this.docontrol = false
    }
  }

}
</script>

<style  scoped>
.headerleft {
  width: 220px;
  padding-top: 15px;
  padding-right: 15px;
  box-sizing: border-box;
}
.header {
  width: 100%;
  position: relative;
  z-index: 1000;
}
.headerrow {
  width: 100%;
  background-color: #fff;
}
.el-menu-item {
  font-size: 13px;
  font-weight: 600;
}
.el-menu.el-menu--horizontal {
  border: none;
}
.headerright {
  padding: 15px;
  font-size: 13px;
}
.phonenum {
  float: right;
  margin-left: 10px;
  transform: translateY(5px);
  color: rgba(0, 0, 0, 0.6);
}
.loginbtn {
  float: right;
  margin-left: 15px;
  border: 1px solid #ea5413;
  color: #ea5413;
}
</style>


