import Vue from 'vue';
import axios from 'axios';
import qs from 'qs';//引入qs，axios在post参数时，要通过qs来格式化数据
axios.defaults.baseURL = process.env.BASE_API;
axios.defaults.headers['Content-Type'] = 'application/x-www-form-urlencoded';
// 添加请求拦截器
axios.interceptors.request.use(
    config => {
        if (config.method == 'post') {
            config.data = qs.stringify(config.data);//对参数进行序列化
        }
        // 添加token更新
        let userInfo = eval('(' + localStorage.getItem('userInfo') + ')');
        if (userInfo) {
            let timer = Number(userInfo.expire) - Math.floor(new Date().getTime() / 1000);
            let header = {
                "Content-Type": "application/x-www-form-urlencoded",
                token: localStorage.getItem('token'),
            }
            if (timer / 60 > 0) {
                if (Math.floor(timer / 60) < 5) {
                    //请求获取新token接口
                    axios({
                        url: "/User/getToken",
                        method: "GET",
                        async: false,
                        contentType: false,
                        processData: false,
                        headers: header,
                    })
                        .then((res) => {
                            console.log(res)
                            if (res.data.code == 200) {
                                localStorage.setItem('token', res.data.data.token)
                            } else {

                            }
                        })
                        .catch((err) => {
                            console.log(err);
                        });
                }
            } else {
                localStorage.clear()
                window.location.href = process.env.BASE_homepage
                window.location.reload()
            }
        }
        return config;
    },
    error => {
        console.log('请求出错啦');
        console.dir(error)
        return Promise.reject(error);
    }
);

// 添加响应拦截器
axios.interceptors.response.use(
    response => {
        return response;
    },
    error => {
        return Promise.reject(error);
    }
);
Vue.prototype.$axios = axios;


