            </div>
            <p><i class="el-icon-location" style="color:#EA5413;font-size:20px"></i>北京市朝阳区四惠蜂巢传媒艺术工作区7-10</p>
            <div>
              <div class="rominfobox">
                <div class="rominfo">
                  可租房间数:<span>{{' ' + info.rooms_number+'个'}}</span>
                </div>
                <div class="rominfo">
                  可租会议室数:<span>{{' ' +info.meets_number+'个'}}</span>
                </div>
              </div>
              <div class="rominfobox">
                <div class="rominfo">
                  装修情况:<span> 高端装修</span>
                </div>

                <div class="rominfo">
                  ⼯商注册:<span> 可注册</span>
                </div>

              </div>
              <div class="rominfos" style="margin-top:10px;">
                <span class="rominfostitle">交通：</span><span> 地铁一号线四惠站、四惠东到项目步行7-10分钟。</span>
              </div>
              <div class="rominfos">
                <span class="rominfostitle">停车：</span><span> 地上停车150元/月，地下停车300元/月</span>
              </div>
            </div>

            <p v-cloak>￥{{info.low_rent +'-'+ info.high_rent +'/间/月'}} </p>

            <div class="gomeet">
              <el-button @click="opendiaglog">
                预约参观
              </el-button>
            </div>
          </div>
          <div class="sTitle">基础设施</div>
          <div class="iconlist">
            <div class="icon" v-for="(it,index) in icon1" :key="index">
              <img :src="it.icon">
              <span>{{it.name}} </span>
            </div>
          </div>
          <div class="sTitle">配套服务</div>
          <div class="iconlist" style="height:100px">
            <div class="icon" v-for="(it,index) in icon2" :key="index">
              <img :src="it.icon">
              <span>{{it.name}} </span>
            </div>
          </div>
          <div class="sTitle">特色服务</div>
          <div class="iconlist" style="height:100px">
            <div class="icon" v-for="(it,index) in icon3" :key="index">
              <img :src="it.icon">
              <span>{{it.name}} </span>
            </div>
          </div>
          <div class="sTitle">写字楼概况</div>
          <div class="introduce">
            <div class="buildinginfoleft">
              <p>
                NICE WORK新生代文创企业聚集地坐落于蜂巢传媒艺术工作区内，该工作区总建筑面积10000平方米，地处长安街头排，国贸正东2000米，紧邻东四环，处于地铁一号线与城铁八通线的首发站之上，工作区内由51-857平米多元空间组成，层高4.8米。
              </p>
              <p>
                该工作区致力于打造符合北京市文化创意产业功能区政策要求、有社会影响力和竞争力的文创产业协同产业园区。工作区内以广告设计、摄影、影视制作、教育培训等传媒艺术业态为主.
              </p>
              <div style="height:230px;">
              </div>
              <div class="buildinginfobox">
                     <img :src="mapimg" style="height:150px;width:100%">
                <span>总建筑面积：</span><span class="buildinginfonum">10000</span><span>平方米</span>
              </div>
              <div class="buildinginfobox">
                <span>多元空间：</span><span class="buildinginfonum">51-857</span><span>平米</span>
              </div>
              <div class="buildinginfobox">
                <span>最高：</span><span class="buildinginfonum">4.8</span><span>米</span>
              </div>
            </div>
            <div class="buildingright">
              <img :src="buildingimg" style="width:100%">
              <img :src="buildingimg2" style="width:100%">
            </div>
          </div>
        </div>
      </el-col>
    </el-row>
    <!-- 对话框 -->
    <el-dialog title="预约参观" :visible.sync="dialogFormVisible" width="450px" :close-on-click-modal='false'>
      <el-form :model="appiontInfo" :rules="rules" style="padding:0 20px" ref="second">
        <el-form-item class="formItem" prop="nickname">
          <el-input maxlength="8" v-model="appiontInfo.nickname" placeholder="您的称谓（选填）"></el-input>
        </el-form-item>
        <el-form-item class="formItem" prop="phone">
          <el-input maxlength="11" v-model.number="appiontInfo.phone" placeholder="您的手机号码"></el-input>
        </el-form-item>
        <el-form-item class="formItem" prop="visit_time">
          <el-date-picker :editable="false" style="width:100%" v-model="appiontInfo.visit_time" type="date" placeholder="选择日期" format="yyyy 年 MM 月 dd 日" value-format="yyyy-MM-dd" :picker-options="pickerOptions">
          </el-date-picker>
        </el-form-item>
        <el-form-item class="formItem" prop="remark">
          <div class="textare">{{appiontInfo.remark.length}}/200 </div>
          <el-input maxlength="200" resize="none" :rows="5" type="textarea" v-model="appiontInfo.remark" placeholder="请输入留言内容（选填）"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer" style="padding:0 20px">
        <el-button class="btusubmit" @click="submitAppiont" :loading='iSDone'>提交</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import introduce from '../assets/web/gywm_img.png'
import buildingimg from '../assets/web/kzhy_fst.png'
import buildingimg2 from '../assets/web/lhbgxq_dxkj.png'
import mapimg from '../assets/web/img_xdt.png'
import ban1 from '../assets/uni/xq_img_bggs.png'
import ban2 from '../assets/uni/xq_img_bgs.png'
import ban3 from '../assets/uni/xq_img_bgsn.png'
import ban4 from '../assets/uni/xq_img_bgsnw.png'
import ban5 from '../assets/uni/xq_img_bgss.png'
import ban6 from '../assets/uni/xq_img_gs.png'
import ban7 from '../assets/uni/xq_img_hyssj.png'
import ban8 from '../assets/uni/xq_img_kt.png'
import ban9 from '../assets/uni/xq_img_sjhys.png'
import ban10 from '../assets/uni/xq_img_snd.png'
import ban11 from '../assets/uni/xq_img_zl.png'
import ban12 from '../assets/uni/xq_img_zldt.png'
import icon11 from '../assets/web/1-1.png'
import icon12 from '../assets/web/1-2.png'
import icon13 from '../assets/web/1-3.png'
import icon14 from '../assets/web/1-4.png'
import icon15 from '../assets/web/1-5.png'
import icon16 from '../assets/web/1-6.png'
import icon17 from '../assets/web/1-7.png'
import icon18 from '../assets/web/1-8.png'
import icon19 from '../assets/web/1-9.png'
import icon110 from '../assets/web/1-10.png'
import icon21 from '../assets/web/2-1.png'
import icon23 from '../assets/web/2-3.png'
import icon24 from '../assets/web/2-4.png'
import icon25 from '../assets/web/2-5.png'
import icon31 from '../assets/web/3-1.png'
import icon32 from '../assets/web/3-2.png'
import icon33 from '../assets/web/3-3.png'
import icon34 from '../assets/web/3-4.png'
import icon35 from '../assets/web/3-5.png'
import accesscontrol from '../assets/web/znmj.png'
import attendance from '../assets/web/smkq.png'
import directdrink from '../assets/web/zys.png'
import greenplants from '../assets/web/xq_icon_lz.png'

export default {
  data () {
    let checkPhone = (rule, value, callback) => {
      let reg = /^1[2345789]\d{9}$/
      if (!reg.test(value)) {
        callback(new Error('请输入11位手机号'))
      } else {
        callback()
      }
    };
    return {
      iSDone:false,
      //验证表单
      rules: {
        phone: [{
          type: 'number',
          validator: checkPhone,
          required: true,
          message: '请输入正确格式的手机号',
          trigger: 'blur'
        }],
        visit_time: [{
          required: true,
          message: '请选择参观日期',
          trigger: 'blur'
        }],
        remark: [{
          message: '',
          trigger: 'blur'
        }],
      },
      //日期选择
      pickerOptions: {
        disabledDate (v) {
          return v.getTime() < new Date().getTime() - 86400000;
        },
      },
      appiontInfo: {
        nickname: "",
        phone: "",
        visit_time: new Date().Format('yyyy-MM-dd'),
        remark: "",
      },
      dialogFormVisible: false,
      introduce, buildingimg, buildingimg2, mapimg,
      banners: [ban1, ban2, ban3, ban4, ban5, ban6,ban7,ban8,ban9,ban10,ban11,ban12],
      info: {},
      icon1: [
        {
          icon: accesscontrol,
          name: '智能门禁'
        },
        {
          icon: attendance,
          name: '扫码考勤'
        },

        {
          icon: icon15,
          name: '超大LED屏'
        },
        {
          icon: icon16,
          name: '视频会议'
        },
        {
          icon: icon19,
          name: '新风系统'
        },
        {
          icon: icon110,
          name: '咖啡机'
        },
        {
          icon: icon17,
          name: '打印'
        },
        {
          icon: icon18,
          name: '传真'
        },
        {
          icon: icon33,
          name: '24H空调'
        },
        {
          icon: directdrink,
          name: '直饮水'
        },
        {
          icon: icon14,
          name: '无线/专线网络'
        },
        {
          icon: icon11,
          name: '网络端口'
        },
        {
          icon: icon13,
          name: '座机端口'
        },
        {
          icon: icon12,
          name: '电源端口'
        },

      ],
      icon2: [{
        icon: icon23,
        name: '前台服务'
      },
      {
        icon: greenplants,
        name: '绿植租摆'
      },

      {
        icon: icon24,
        name: '保洁服务'
      },
      {

        icon: icon21,
        name: '保安'
      },
      {
        icon: icon25,
        name: '设备维护'
      }
      ],
      icon3: [{
        icon: icon31,
        name: '特色咖啡茶饮'
      },
      {
        icon: icon32,
        name: '主题活动'
      },

      {
        icon: icon34,
        name: '阅读区'
      },
      {
        icon: icon35,
        name: '自动贩卖系统'
      }
      ]
    }
  },
  methods: {
    opendiaglog(){
      this.dialogFormVisible = true;
      this.appiontInfo.nickname ='';
      this.appiontInfo.phone = '';
      this.appiontInfo.remark = '';
      this.appiontInfo.visit_time = new Date().Format('yyyy-MM-dd');
    },
    submitAppiont () {
      let vaild = '';
      this.$refs['second'].validate((valid) => {
        if (valid) {
          vaild = true
        } else {
          vaild = false
          return false;
        }
      });
      if (vaild == true) {
        
      this.iSDone =true;
        setTimeout(() => {
                  this.$axios.post("/Portal/UnionOffice/visitAdd", this.appiontInfo).then((res) => {
          if (res.data.code == 200) {
            this.$message({
              type: 'success',
              message: '预约成功!'
            });
            this.$refs['second'].resetFields();
            this.dialogFormVisible = false;
          } else {
            this.$message({
              type: 'error',
              message: res.data.msg
            })
          }
          this.iSDone =false;
        });
        }, 500);

      }
    },
    //获取详情
    getOfInfo () {
      let params = {
        // high_rent: "25200.00"
        // low_rent: "0.00"
        // meets_number: 1
        // rooms_number: 2
      }
      this.$axios.get("/Portal/UnionOffice/detail", {}).then((res) => {
        if (res.data.code == 200) {
          this.info = res.data.data;
        } else {
          console.error(res.data.msg, res);
        }
      });
    },
    getSwiper () {
      let certifySwiper = new Swiper('#certify .swiper-container', {
        watchSlidesProgress: true,
        slidesPerView: 1.5,
        centeredSlides: true,
        loop: true,
        // loopedSlides: 5,
        autoplay: {
          delay: 5000,//1秒切换一次
        },
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev',
        },
        on: {
          progress: function (progress) {
            for (let i = 0; i < this.slides.length; i++) {
              var slide = this.slides.eq(i);
              var slideProgress = this.slides[i].progress;
              let modify = 1;
              if (Math.abs(slideProgress) > 1) {
                modify = (Math.abs(slideProgress) - 1) * 0.3 + 1;
              }
              let translate = slideProgress * modify * 260 + 'px';
              let scale = 1 - Math.abs(slideProgress) / 5;
              let zIndex = 999 - Math.abs(Math.round(10 * slideProgress));
              slide.transform('translateX(' + translate + ') scale(' + scale + ')');
              slide.css('zIndex', zIndex);
              slide.css('opacity', 1);
              if (Math.abs(slideProgress) > 3) {
                slide.css('opacity', 0);
              }
            }
          },
          setTransition: function (transition) {
            for (var i = 0; i < this.slides.length; i++) {
              var slide = this.slides.eq(i)
              slide.transition(transition);
            }
          }
        }
      })
    }
  },
  mounted () {
    this.getSwiper();
    this.getOfInfo();
  }
}
</script>
<style>
/* swiper */
#certify {
  position: relative;
  width: 1000px;
  margin: 0 auto;
  user-select: none;
}
#certify .swiper-container {
  margin: 20px;
}
#certify .swiper-slide {
  width: 700px;
  height: 300px;
  background: #fff;
  box-shadow: 0 8px 30px #ddd;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 2px 2px 6px rgba(75, 74, 74, 0.863);
}
#certify .swiper-slide img {
  display: block;
  height: 100%;
  width: 100%;
  border-radius: 10px;
}
#certify .swiper-pagination {
  width: 100%;
  bottom: 20px;
}
#certify .swiper-button-prev:after {
  color: rgba(255, 255, 255, 0.9);
  text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.562);
  transform: translateX(-30px);
}
#certify .swiper-button-next:after {
  color: rgba(255, 255, 255, 0.9);
  text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.582);
  transform: translateX(30px);
}
#certify .swiper-slide-prev {
  /* 蒙版  :after*/
  /* content: '';
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        height: 100%; */
  /* background-color: rgba(255, 255, 255, 0.363); */
  /* 模糊 */
  filter: blur(2px);
}
#certify .swiper-slide-next {
  filter: blur(2px);
}
</style>
<style scoped lang="scss">
