  display: none;
}
$orange: #EA5413;
.btusubmit {
  width: 100%;
  // padding: 15px 0;
  background-color: $orange;
  color: #ffffff;
  font-size: 16px;
  font-weight: 600;
  letter-spacing: 10px;
  -webkit-text-stroke: 0.4px $orange;
}
.textare {
  position: absolute;
  right: 10px;
  bottom: -5px;
  z-index: 99;
  color: #a3a2a2;
}
>>> .el-dialog__body {
  padding-bottom: 0px;
}
>>> .el-dialog__header {
  padding-left: 70px;
  padding-top: 30px;
  position: relative;
  font-weight: 600;
}
>>> .el-dialog__header .el-icon-close {
  color: $orange;
  font-size: 20px;
  font-weight: 600;
}
>>> .el-dialog__header::before {
  content: "";
  width: 100px;
  height: 100px;
  position: absolute;
  left: 10px;
  top: -13px;
  background: url("../assets/web/yycg_pop_logo.png") no-repeat;
}
.introduce p {
  text-indent: 2em;
}
.introduce img {
  margin: 10px 0;
}
.introduce {
  color: rgba(0, 0, 0, 0.6);
  font-size: 14px;
  line-height: 23px;
  margin: 15px 0;
}
.icon {
  box-sizing: border-box;
  max-width: 135px;
  min-width: 135px;
  height: 100px;
  flex: 1;
  display: inline-block;
  padding: 20px 0;
}
.icon img,
.icon span {
  display: block;
}
.icon span {
  text-align: center;
  margin-top: 10px;
  color: #b3b1b1;
  font-size: 14px;
}
.icon img {
  margin: 0 auto;
  width: 32px;
  height: 32px;
}
.priceInfo .iconlist {
  // justify-content: space-between;
  width: 100%;
  height: 200px; // display: flex;
  // flex-direction: row;
  //         flex-wrap: wrap;
}
.sTitle {
  padding: 4px 20px;
  color: #000;
  font-size: 15px;
  font-weight: 600;
  background-color: #f2f2f2;
  position: relative;
  margin: 20px 0 0 0;
}
.sTitle:after {
  content: "";
  position: absolute;
  width: 8px;
  height: 100%;
  left: 0;
  top: 0;
  background-color: $orange;
}
.uni {
  width: 100%;
}
.ibox {
  width: 1000px;
  margin-top: 0px;
}
.hander {
  width: 100%;
  background-color: #d2d2d2;
}
.fTitle {
  position: relative;
}
.fTitle .gomeet button {
  position: absolute;
  padding: 11px 50px;
  right: 0px;
  bottom: 0px;
  background-color: $orange;
  color: #ffffff;
  font-size: 18;
  -webkit-text-stroke: 0.1px #ffffff;
  font-weight: 500;
  letter-spacing: 1.5px;
}
/*大标题 */
.fTitle p:first-of-type {
  width: 70%;
  font-size: 18px;
  font-weight: 600;
  line-height: 30px;
  text-overflow: -o-ellipsis-lastline;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
/*位置 */
.fTitle p:nth-of-type(1) {
  margin: 15px 0;
  font-size: 14px;
  color: #000;
}
/*价格 */
.fTitle p:nth-of-type(2) {
  margin: 10px 0;
  color: $orange;
  font-size: 19px;
  font-weight: 600;
  letter-spacing: 1px;
}
/*标签 */
.fTitle div .tagInfo {
  color: $orange;
  margin-right: 5px;
  margin-top: 10px;
  font-size: 14px;
  display: inline-block;
  padding: 2px 10px;
  border: $orange 1px solid;
  border-radius: 5px;
}
/*文字信息 */
/*文字信息 */
.fTitle div .rominfobox {
  display: inline-block;
  margin-left: 30px;
}

.fTitle div .rominfo {
  line-height: 25px;
  font-size: 14px;
  width: 150px;
  color: #888787;
}

.fTitle div .rominfos {
  line-height: 25px;
  font-size: 14px;
  color: rgba(65, 64, 64, 0.918);
}

.fTitle div .rominfo > span,
.rominfostitle {
  color: rgb(0, 0, 0);
  font-weight: 600;
}
.infotitle {
  font-size: 17px;
}
.buildinginfoleft {
  display: inline-block;
  width: 25%;
  line-height: 25px;
  color: #888787;
}
.buildingright {
  display: inline-block;
  width: 74.5%;
}
.buildinginfobox {
  transform: translateY(-80px);
    line-height: 30px;
  color: rgba(65, 64, 64, 0.918);
}
.buildinginfonum {
  color: rgba(65, 64, 64, 0.918);
  font-size: 19px;
  font-weight: 600;
}
</style>


