<template>
  <div class="index">
    <el-row type="flex" class="row-bg" justify="center">
      <el-col :span="24">
        <div
          v-swiper:mySwiper="swiperOption"
          style="position: relative; z-index: 10"
        >
          <div class="swiper-wrapper">
            <div
              class="swiper-slide"
              :key="banner + i"
              v-for="(banner, i) in banners"
            >
              <div
                class="bannerImg"
                @click="open(banner.link_url)"
                :style="{
                  background: 'url(' + banner.image + ') no-repeat center'
                }"
              ></div>
              <!-- <img :src="banner.image" class="bannerImg"  /> -->
            </div>
          </div>
          <div
            class="swiper-pagination"
            style="transform: translateY(-40px)"
          ></div>
        </div>

        <el-col style="width:1100px" class="fixposition">
          <div
            class="swiper-button-prev orangeee"
            style="transform: translateY(-180px);outline: none;"
          ></div>
          <div
            class="swiper-button-next orangeee"
            style="transform: translateY(-180px);outline: none;"
          ></div>
          <div class="appiontment">
            <el-form
              ref="appiontInfo"
              :rules="rules"
              :model="appiontInfo"
              label-width="20px"
              class="formdiv"
              size="medium"
            >
              <el-row :gutter="10">
                <el-col :span="4">
                  <el-form-item class="formItem" prop="nickname">
                    <el-input
                      v-model="appiontInfo.nickname"
                      placeholder="您的称谓（选填）"
                    ></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="4">
                  <el-form-item class="formItem" prop="phone">
                    <el-input
                      maxlength="11"
                      oninput="value=value.replace(/[^\d]/g,'')"
                      v-model.number="appiontInfo.phone"
                      placeholder="您的手机号码"
                    ></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="5">
                  <el-form-item class="formItem" prop="visit_time">
                    <el-date-picker
                      v-model="appiontInfo.visit_time"
                      type="date"
                      placeholder="选择日期"
                      format="yyyy 年 MM 月 dd 日"
                      value-format="yyyy-MM-dd"
                      :picker-options="pickerOptions"
                    >
                    </el-date-picker>
                  </el-form-item>
                </el-col>
                <el-col :span="7">
                  <el-form-item class="formItem" prop="remark">
                    <el-input
                      v-model="appiontInfo.remark"
                      placeholder="请输入留言内容（选填）"
                    ></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span="4">
                  <el-form-item class="formItem" size="medium">
                    <el-button
                      type="primary"
                      @click="submitAppiont"
                      :loading="iSDone"
                      style="margin-left:20px"
                      >预约参观</el-button
                    >
                  </el-form-item>
                </el-col>
              </el-row>
            </el-form>
          </div>
        </el-col>
      </el-col>
    </el-row>
    <div class="iboxindex">
      <el-row type="flex" class="row-bg" justify="center">
        <el-col style="width:1100px;height: 100%">
          <div class="articlecon">
            <p>联合办公区</p>
            <div class="uniinfo">
              <div>
                <el-image :src="price" />
              </div>
              <div
                class="getMoreinfor"
                @click="$router.push({ name: 'uniDetails' })"
              >
                了解更多
              </div>
              <div class="infouni">
                <p>
                  43个独立办公室，办公配套设施完善，在这里您可以享受高科技的智能化办公空间、资源共享空间以及全要素配套服务，为中小型企业团队提供一站式办公服务解决方案。
                </p>
                <p>适合这样的你：</p>
                <div>
                  <p class="infodetails">·初创团队</p>
                  <p class="infodetails">·机构办事处</p>
                  <p class="infodetails">·企业分公司</p>
                  <p class="infodetails">·大公司独立业务部门</p>
                </div>
                <div>
                  ￥{{ infoindex.low_rent + "-" + infoindex.high_rent }}/间/月
                </div>
              </div>
            </div>
          </div>
          <div class="articlecon env">
            <p>办公环境</p>
            <div>
              <div @click="godetials(1)">
                <el-image :src="roadshowHall" alt=""> </el-image>
                <span>会议室 - 路演厅</span>
              </div>
              <div @click="godetials(2)">
                <el-image :src="negotiationHall" alt=""> </el-image>
                <span>会客区 - 洽谈区</span>
              </div>
            </div>
            <div>
              <div @click="godetials(3)">
                <el-image :src="vendingMachine" alt=""> </el-image>
                <span>水吧台 - 贩售机</span>
              </div>
              <div @click="godetials(4)">
                <el-image :src="corridor" alt=""> </el-image>
                <span>园区走廊 - 空中花园</span>
              </div>
            </div>
          </div>
          <div class="articlecon garden">
            <p style="position:relataive;">
              园区资讯
              <span class="gardenmore" @click="$router.push({ name: 'notice' })"
                >查看更多<i
                  class="el-icon-arrow-right"
                  style="color:#EA5413;font-size:20px;vertical-align:sub"
                ></i>
              </span>
            </p>
            <div class="">
              <div class="noticecard">
                <el-card
                  class="cards"
                  :key="index + 'newslist'"
                  v-for="(i, index) in newslist"
                >
                  <el-image
                    :src="i.image"
                    class="cardimage"
                    @click="goInfodetails(i)"
                  >
                    <div slot="error" class="image-slot">
                      <div
                        style="width: 100%;height: 140px;text-align: center;position:relative;border: 1px dashed #ccc;background:#d2d2d2d2;"
                      >
                        <i
                          class="el-icon-picture-outline"
                          style="margin-top: 70px;"
                        ></i>
                      </div>
                    </div>
                  </el-image>
                  <div style="padding: 10px 20px" @click="goInfodetails(i)">
                    <h3 class="cardtitle">{{ i.title }}</h3>
                    <span class="cardcontent">{{ i.describe }}</span>
                    <div class="cardfooter">
                      <span class="readtext readbtn" @click="goInfodetails(i)"
                        >阅读全文</span
                      >
                    </div>
                  </div>
                </el-card>
              </div>
            </div>
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import { Swiper, SwiperSlide, directive } from "vue-awesome-swiper";
import "@/css/swiper.css";
import banner1 from "@/assets/web/banner_one.png";
import price from "@/assets/web/home_bgkj_bg.png";
import roadshowHall from "@/assets/web/home_img_hys.png";
import negotiationHall from "@/assets/web/home_img_hkq.png";
import vendingMachine from "@/assets/web/home_img_sbt.png";
import corridor from "@/assets/web/kzhy_dykj.png";
import b444 from "@/assets/web/home_bghj_bj.png";
import { formatTimeToStr } from "@/utils/date.js";
export default {
  data() {
    return {
      iSDone: false,
      //验证表单
      rules: {
        phone: [
          {
            required: true,
            message: "请输入手机号码",
            trigger: "blur"
          }
        ],
        visit_time: [
          {
            required: true,
            message: " ",
            trigger: "blur"
          }
        ],
        remark: [
          {
            message: "",
            trigger: "blur"
          }
        ]
      },
      noticetypes: [],
      type: "",
      page: 1,
      pagesize: 4,
      newslist: [],
      // banner
      b444,
      price,
      roadshowHall,
      negotiationHall,
      vendingMachine,
      corridor,
      banners: [{ image: banner1, link_url: "" }],
      swiperOption: {
        // loop : true,
        //分页器
        pagination: {
          el: ".swiper-pagination",
          clickable: true,
          // active的样式
          bulletActiveClass: "my-bullet-active"
        },
        // 底部圆点导航
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev",
          disabledClass: "my-button-disabled"
        },
        //自动切换
        autoplay: {
          delay: 5000
        }
      },
      //预约信息
      appiontInfo: {
        nickname: "",
        phone: "",
        visit_time: new Date().Format("yyyy-MM-dd"),
        remark: ""
      },
      //日期选择
      pickerOptions: {
        disabledDate(v) {
          return v.getTime() < new Date().getTime() - 86400000;
        }
      },
      infoindex: {}
    };
  },
  methods: {
    //咨询跳转详情
    // 跳转详情页
    goInfodetails(item) {
      if (item.content_type == 1) {
        this.$router.push({ name: "noticeDetails", query: { id: item.id } });
      } else {
        window.open(item.link_url);
      }
    },
    open(url) {
      if (url != "") {
        window.open(url);
      }
    },
    getban() {
      this.$axios.get("/Portal/Advert/AdgetList?type=web").then(res => {
        if (res.data.code == 200) {
          if (res.data.data.list && res.data.data.list.length > 0) {
            this.banners = res.data.data.list;
          } else {
            this.banners = [{ image: banner1, link_url: "" }];
          }
        } else {
          console.error(res.data.msg, res);
        }
      });
    },
    godetials(sort) {
      switch (sort) {
        case 1:
          this.$router.push({ name: "dePerform", query: {} });
          break;
        case 2:
          this.$router.push({ name: "deMeet", query: {} });
          break;
        case 3:
          this.$router.push({ name: "deSell", query: {} });
          break;
        case 4:
          this.$router.push({ name: "deGarden", query: {} });
          break;

        default:
          break;
      }
    },
    getOfInfo() {
      let params = {
        // high_rent: "25200.00"
        // low_rent: "0.00"
        // meets_number: 1
        // rooms_number: 2
      };
      this.$axios.get("/Portal/UnionOffice/detail", {}).then(res => {
        if (res.data.code == 200) {
          this.infoindex = res.data.data;
        } else {
          console.error(res.data.msg, res);
        }
      });
    },
    submitAppiont() {
      let vaild = "";
      this.$refs["appiontInfo"].validate(valid => {
        if (valid) {
          vaild = true;
        } else {
          vaild = false;
          return false;
        }
      });
      if (vaild == true) {
        this.iSDone = true;
        setTimeout(() => {
          this.$axios
            .post("/Portal/UnionOffice/visitAdd", this.appiontInfo)
            .then(res => {
              if (res.data.code == 200) {
                this.$message({
                  type: "success",
                  message: "预约成功!"
                });
                this.$refs["appiontInfo"].resetFields();
              } else {
                this.$message({
                  type: "error",
                  message: res.data.msg
                });
              }
              this.iSDone = false;
            });
        }, 500);
      }
    },
    //   获取资讯类型
    getnewstype() {
      this.$axios.get("/Portal/News/Type", {}).then(res => {
        this.noticetypes = res.data.data;
        this.changetype("0", this.noticetypes[0].id);
      });
    },
    //
    changetype(index, type) {
      this.type = type;
      this.activeClass = index;
      let params = {
        type: Number(type),
        page: this.page,
        page_size: this.pagesize
      };
      this.$axios
        .get("/Portal/News/getList", {
          params: params
        })
        .then(res => {
          this.total = res.data.data.total;
          this.newslist = res.data.data.list;
        });
    }
  },
  components: {
    Swiper,
    SwiperSlide
  },
  directives: {
    swiper: directive
  },
  created() {
    if (window.location.host.indexOf("wwww") == -1) {
      window.location.host = 'www.nicework.net.cn'
      window.reload()
    }
  },
  mounted() {
    this.getban();
    this.getnewstype();
    //切换到第三个 1秒 禁用transtion回调
    this.mySwiper.slideTo(0, 1000);
    this.getOfInfo();
  }
};
</script>
<style>
/* 设置分页器样式 */
.my-bullet-active {
  background: #ea5413 !important;
  opacity: 1 !important;
}
.my-button-disabled {
  cursor: no-drop !important;
  color: #c0c0c0 !important;
  text-shadow: 1px 0 2px #00000030 !important;
}
</style>
<style lang="scss" scoped>
.noticecard {
  display: flex;
  justify-content: space-between;
  margin: 10px 0;
}
$orange: #ea5413;
.fixposition {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  z-index: 11;
}
/* 园区咨询*/
.gardenmore {
  position: absolute;
  right: 20px;
  font-size: 13px;
  font-weight: 500;
  color: rgba(0, 0, 0, 0.6);
  cursor: pointer;
}
>>> .el-image {
  height: 100%;
  width: 100%;
}
.cards {
  width: calc(25% - 15px);
  margin: 10px 0px;
  border-radius: 10px;
  display: inline-block;
  cursor: pointer;
}
.cardimage {
  width: 100%;
  height: 170px;
}
>>> .el-card__body {
  padding: 0px !important;
}
.cardcontent {
  font-size: 12px;
  width: 100%;
  height: 50px;
  color: rgba(0, 0, 0, 0.6);
  word-break: normal;
  width: auto;
  display: -webkit-box;
  white-space: pre-wrap;
  word-wrap: break-word;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  margin-bottom: 10px;
}
.cardfooter {
  position: relative;
}
.readtext {
  font-size: 13px;
}
.readbtn {
  float: right;
  margin-bottom: 10px;
  cursor: pointer;
}
.cardtitle {
  height: 40px;
  line-height: 20px;
  display: -webkit-box;
  word-break: normal;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  margin-bottom: 5px;
}
/*办公环境 */
.env > div {
  height: 270px;
  width: 100%;
  margin: 10px 0;
  margin-bottom: 50px;
  display: flex;
  justify-content: space-between;
  box-sizing: border-box;
}
.env > div > div {
  cursor: pointer;
  position: relative;
  width: 47%;
  height: 100%;
}
>>> .env > div > div .el-image__inner {
  width: 100%;
  height: 100%;
  border-radius: 10px;
}
.env > div > div > span {
  position: absolute;
  bottom: -30px;
  font-size: 13px;
  left: 50%;
  transform: translateX(-50%);
  color: rgba(82, 82, 82, 0.726);
}
/* 联合办公*/
.articlecon {
  width: 100%;
  position: relative;
  margin: 10px 0;
}
.getMoreinfor {
  position: absolute;
  border-radius: 5px;
  padding: 5px 14px;
  right: 10px;
  font-weight: 600;
  cursor: pointer;
  font-size: 13px;
  bottom: 15px;
  color: $orange;
  background-color: #fff;
}
.iboxindex div.articlecon > p {
  font-weight: 600;
}
.infouni > p {
  font-size: 13px;
  font-weight: 600;
  line-height: 25px;
}
.infouni > div > p {
  font-size: 11px;
  line-height: 17px;
  color: rgba(255, 255, 255, 0.801);
}
.infouni > div:last-of-type {
  position: absolute;
  font-size: 20px;
  left: 20px;
  bottom: 15px;
}
.blueinfo {
  margin-top: 10px;
  display: inline-block;
  padding: 2px 10px;
  border: #fff 1px solid;
  border-radius: 5px;
}
.uniinfo {
  height: 280px;
  width: 100%;
  margin-top: 10px;
  position: relative;
  border-radius: 10px;
  overflow: hidden;
}
.uniinfo img {
  height: 100%;
  width: 100%;
}
.uniinfo > div:first-of-type {
  height: 100%;
}
.uniinfo > .infouni {
  width: 100%;
  height: 100%;
}
.uniinfo > div:last-of-type {
  position: absolute;
  color: #fff;
  font-size: 13px;
  left: 0;
  top: 0;
  background-color: rgba(234, 84, 19, 0.8);
  width: 35%;
  height: 100%;
  padding: 20px;
  box-sizing: border-box;
}
.index {
  position: relative;
  background-color: #fff;
  padding-bottom: 30px;
}
>>> .el-input__inner:focus {
  border: $orange 1px solid;
}
.orange {
  color: $orange;
}
.orangeee {
  color: #ebebeb;
  text-shadow: 2px 2px 2px #41414185;
}
.bannerImg {
  cursor: pointer;
  max-width: 100%;
  margin: 0 auto;
  height: 330px;
  position: relative;
}
.bannerImg img {
  width: auto !important;
}
.appiontment {
  position: relative;
  z-index: 11;
  width: 100%;
  height: 60px;
  background-color: #fff;
  transform: translateY(-40px);
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.13);
  border-radius: 8px;
}
.iboxindex {
  margin-top: -30px;
}
.formItem {
  display: inline-block;
  width: 100%;
}
.formItem button {
  background-color: $orange;
  border: none;
}
.formItem button:hover {
  opacity: 0.8;
}
.formdiv {
  line-height: 60px;
}
.infodetails {
  line-height: 25px !important;
}
</style>



