<template>
  <div class="perform ibox">
     <div>会议室 路演厅</div>
     <div>
         <p>可容纳10-60人的多类型会议室可供选择，入驻企业每月有免费使用时长，会议室配备WIFI、共享投影、远程会议等办公设备。</p>
         <div class="pic">
             <div class="fr">
                 <div><img :src="pic1"></div>
                 <div><img :src="pic2"></div>
             </div>
             <img :src="pic3">
         </div>
     </div>
  </div>
</template>

<script>
import pic1 from '../../assets/de/hys_bgs.png'
import pic3 from '../../assets/de/hys_dt.png'
import pic2 from '../../assets/de/hys_lyt.png'
export default {
    data(){
        return{
            pic1,pic2,pic3
        }
    },
}
</script>

<style scoped lang="scss">
    .perform>div:first-of-type{
        font-size: 20px;
        font-weight: 600;
        margin: 10px 0;
    }
    .perform>div>p{
        text-indent: 2em;
        color: #414141;
        margin: 20px 0;
    }
    .perform{
        width: 1000px;
        margin: 10px auto;padding: 30px;
        box-sizing: border-box;
    }
    .fr{
        display:flex ;
        justify-content: space-between;
        margin-bottom: 20px;
    }
    .fr>div{
        width: 49%;
    }
    .pic img{
        border-radius: 10px;
    }
    .pic img:nth-of-type(1),.pic img:nth-of-type(2){
        width: 100%;
    }
</style>


