<template>
  <div class="allFooter">
    <el-row type="flex" class="row-bg" justify="center">
      <el-col type="flex" class="row-bg allFooterbox" justify="center">
        <el-row>
          <el-col :span="4">
            <img :src="Nicelogo " width="100%" style="margin-top:25px">
          </el-col>
          <el-col :span="14">
            <div class="statement">
              <p>
                <span @click="$router.push({ name:'about' })">关于我们</span>
                <el-divider direction="vertical"></el-divider>
                <span @click="$router.push({ name:'privacyPolicy' })">隐私条款</span>
                <el-divider direction="vertical"></el-divider>
                <span @click="$router.push({ name:'LegalNotices' })">法律声明</span>
                <el-divider direction="vertical"></el-divider>
                <span @click="$router.push({ name:'contact' })">联系我们</span>
              </p>
              <p>
                咨询电话：{{phonenum}} （早9:30 - 晚17:30）
              </p>
              <p>
                © 2021PricewaterhouseCoopers Ltd. All rights reserved.
              </p>
            </div>
          </el-col>
          <el-col :span="6" class="rightcord">
            <div>
              <div id="contactqrcode"></div>
              <span>NICEWORK APP</span>
            </div>
            <div>
              <div id="contactqrcode1"></div>
              <span>关注NICEWORK公众号</span>
            </div>
          </el-col>
        </el-row>
      </el-col>

    </el-row>
  </div>
</template>

<script>
import Nicelogo from '@/assets/web/home_logo.png'
import ERcordLeft from '@/assets/footer/img_gzh.png'
import QRCode from "qrcodejs2"; // 引入qrcode
export default {
  data () {
    return {
      Nicelogo, ERcordLeft,
      phonenum: '010-85755120'
    }
  },
  methods: {
    opensecrect (url) {
      window.open(url);
    },
    // 获取客服电话
    getphone () {
      this.$axios.get('/Portal/UnionOffice/getPhone', {
      }).then(res => {
        if (res.data.code == 200) {
          this.phonenum = res.data.data.phone;
        } else {
          this.$message({
            type: 'error',
            message: res.data.msg
          })
        }
      });
    },
  },
  mounted () {
    this.getphone();
    new QRCode("contactqrcode", {
      width: 50,
      height: 50,
      correctLevel: 0,
      text: 'http://h5.nicework.net.cn/admin/jumptodownload.html', // 二维码地址
      colorDark: "#000",
      colorLight: "#fff",
    });
    new QRCode("contactqrcode1", {
      width: 50,
      height: 50,
      correctLevel: 0,
      text: 'http://weixin.qq.com/r/hDlqcsHEBJx7rYUR92yW', // 二维码地址
      colorDark: "#000",
      colorLight: "#fff",
    });
  }
}
</script>

<style scoped lang='scss'>
.allFooterbox {
  width: 1000px;
}
.statement p > span:hover {
  color: #fff;
  cursor: pointer;
}
.allFooter {
  width: 100%;
  height: 150px;
  background-color: #727071;
}
.statement {
  margin-top: 15px;
  font-size: 11px;
  color: rgba(255, 255, 255, 0.767);
  line-height: 43px;
  text-align: center;
}
.rightcord {
  display: flex;
  justify-content: space-between;
}
>>> .rightcord img {
  display: inline-flex;
  margin-top: 25px;
  width: 65px;
}
.rightcord > div {
  text-align: center;
}
.rightcord div > span {
  display: inline-block;
  color: rgba(255, 255, 255, 0.767);
  font-size: 11px;
  transform: translateY(-5px);
}
#contactqrcode {
  margin-top: 25px;
  height: 83px;
}
#contactqrcode1 {
  margin-top: 25px;
  height: 83px;
}
>>> #contactqrcode img,
>>> #contactqrcode1 img {
  margin: 0 auto;
  display: inline-flex;
  margin-top: 25px;
  background-color: #fff;
  padding: 3px;
}
</style>


