<template>
  <div class="perform ibox">
     <div>水吧台 贩售机</div>
     <div>
         <p>NICE WORK为园区企业准备了多样化饮品，咖啡、柠檬水、直饮水、茶水免费提供给会员，对于酷爱饮料的年轻伙伴，我们将最新最受欢迎的饮品放进贩售机，并以采购价格让饮料爱好者享受他们的快乐。</p>
         <div class="pic">
             <div class="fr">
                 <div><img :src="pic1"></div>
                 <div><img :src="pic2"></div>
             </div>
             <div class="fr">
                 <div><img :src="pic3"></div>
                 <div><img :src="pic4"></div>
             </div>
         </div>
     </div>
  </div>
</template>

<script>
import pic1 from '../../assets/de/home_img_sbt.png'
import pic2 from '../../assets/de/sbt_kft.png'
import pic3 from '../../assets/de/sbt_fmj.png'
import pic4 from '../../assets/de/sbt_vbl.png'
export default {
    data(){
        return{
            pic1,pic2,pic3,pic4
        }
    },
}
</script>

<style scoped lang="scss">
    .perform>div:first-of-type{
        font-size: 20px;
        font-weight: 600;
        margin: 10px 0;
    }
    .perform>div>p{
        text-indent: 2em;
        color: #414141;
        margin: 20px 0;
    }
    .perform{
        width: 1000px;
        margin: 10px auto;padding: 30px;
        box-sizing: border-box;
    }
    .fr{
        display:flex ;
        justify-content: space-between;
        margin-bottom: 20px;
        height: 250px;
    }
    .fr>div{
        width: 49%;
        height: 100%;
    }
    .pic img{
        border-radius: 10px;
        height: 100%;
    }
    .pic img:nth-of-type(1),.pic img:nth-of-type(2){
        width: 100%;
    }
</style>


